// Mock data for mentor pool and past pairings
const mockMentors: Mentor[] = [
  {
    mentorId: 'mentor-1',
    name: 'Dr. Evelyn Reed',
    title: 'Lead Agricultural Scientist',
    mentorInterests: 'Sustainable farming, crop science, agritech startups',
    mentorBackground: 'PhD in Agronomy, 20+ years of experience in research and development. Passionate about mentoring young innovators in agriculture.',
    avatarUrl: 'https://picsum.photos/seed/20/100/100',
  },
  {
    mentorId: 'mentor-2',
    name: 'Samuel Chen',
    title: 'Software Engineer @ Google',
    mentorInterests: 'Web development, AI/ML, career growth in tech',
    mentorBackground: 'Senior Software Engineer with a decade of experience building large-scale systems. Co-founded a non-profit for teaching code to underprivileged youth.',
    avatarUrl: 'https://picsum.photos/seed/21/100/100',
  },
  {
    mentorId: 'mentor-3',
    name: 'Aisha Jalloh',
    title: 'Founder & CEO, KaziConnect',
    mentorInterests: 'Entrepreneurship, social impact, digital marketing for startups',
    mentorBackground: 'Serial entrepreneur with two successful exits. Focused on building businesses that solve real-world problems in emerging markets. Forbes 30 Under 30.',
    avatarUrl: 'https://picsum.photos/seed/22/100/100',
  },
];

const mockPastPairings = `
- A mentee interested in 'mobile app development' was paired with a 'Senior Mobile Engineer'. Outcome: Mentee successfully launched their first app on the App Store.
- A mentee aiming for 'a career in renewable energy' was paired with a 'Project Manager at a solar energy company'. Outcome: Mentee secured an internship and later a full-time role in the sector.
- A mentee from a rural area interested in 'modernizing their family farm' was paired with an 'Agritech consultant'. Outcome: Mentee implemented a new irrigation system that increased crop yield by 40%.
`;


export async function getMentorSuggestions(menteeProfile: MenteeProfile): Promise<{ success: boolean; suggestions?: MatchResult[]; error?: string; }> {
  try {
    const suggestions = await suggestMentorMatches({
      menteeInterests: menteeProfile.menteeInterests,
      menteeGoals: menteeProfile.menteeGoals,
      menteeBackground: menteeProfile.menteeBackground,
      mentorPool: mockMentors.map(({ mentorId, mentorInterests, mentorBackground }) => ({
        mentorId,
        mentorInterests,
        mentorBackground,
      })),
      pastSuccessfulPairings: mockPastPairings,
    });
    
    // Combine AI suggestions with full mentor details
    const populatedSuggestions: MatchResult[] = suggestions.map(suggestion => {
        const mentorDetails = mockMentors.find(m => m.mentorId === suggestion.mentorId);
        return {
            ...suggestion,
            ...mentorDetails,
        }
    });

    return { success: true, suggestions: populatedSuggestions };
  } catch (error) {
    console.error('Error getting mentor suggestions:', error);
    return { success: false, error: 'Failed to get suggestions. Please try again.' };
  }
}
